.appl1_sgpt_app_settings_wrapper {
    width: 100%;
    overflow-y: scroll;
    height: 552px;
    padding-bottom: 16px;
    scroll-behavior: smooth;
}

.appl1_sgpt_app_settings_wrapper::-webkit-scrollbar-button {
    background-repeat: no-repeat;
    width: 0;
    height: 0;
}

.appl1_sgpt_app_settings_wrapper::-webkit-scrollbar-track {
    background-color: transparent;
    opacity: 0.5;
}

.appl1_sgpt_app_settings_wrapper::-webkit-scrollbar-thumb {
    border-radius: 50px;
    background-color: #592cff;
    opacity: 0.5;
}

.appl1_sgpt_app_settings_wrapper::-webkit-scrollbar-thumb:hover {
    background-color: #592cff;
}

.appl1_sgpt_app_settings_wrapper::-webkit-scrollbar {
    width: 5px;
    height: 5px;
}

