.appl1_sgpt_app_settings_remove_section,
.appl1_sgpt_app_settings_helper_section {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.appl1_sgpt_app_settings_remove_section_switch,
.appl1_sgpt_app_settings_helper_section_switch {
    position: relative;
    display: inline-block;
    width: 35px;
    height: 23px;
    margin-right: 14px;
}

.appl1_sgpt_app_settings_remove_section_switch_input,
.appl1_sgpt_app_settings_helper_section_switch_input {
    opacity: 0;
    width: 0;
    height: 0;
}

.appl1_sgpt_app_settings_remove_section_slider_round,
.appl1_sgpt_app_settings_helper_section_slider_round {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #d8e4e9;
    -webkit-transition: .4s;
    transition: .4s;
}

.appl1_sgpt_app_settings_remove_section_slider_round:before,
.appl1_sgpt_app_settings_helper_section_slider_round::before {
    position: absolute;
    content: "";
    height: 17px;
    width: 17px;
    left: 3px;
    bottom: 3px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
}

.appl1_sgpt_app_settings_remove_section_switch_input:checked+.appl1_sgpt_app_settings_remove_section_slider_round {
    background-color: #592cff;
}

.appl1_sgpt_app_settings_helper_section_switch_input:checked+.appl1_sgpt_app_settings_helper_section_slider_round {
        background-color: #592cff;
}

.appl1_sgpt_app_settings_remove_section_switch_input:checked+.appl1_sgpt_app_settings_remove_section_slider_round:before {
    -webkit-transform: translateX(12px);
    -ms-transform: translateX(12px);
    transform: translateX(12px);
}

.appl1_sgpt_app_settings_helper_section_switch_input:checked+.appl1_sgpt_app_settings_helper_section_slider_round:before { 
    -webkit-transform: translateX(12px);
        -ms-transform: translateX(12px);
        transform: translateX(12px);

}

.appl1_sgpt_app_settings_remove_section_slider_round,
.appl1_sgpt_app_settings_helper_section_slider_round {
    border-radius: 11px;
}

.appl1_sgpt_app_settings_remove_section_slider_round:before,
.appl1_sgpt_app_settings_helper_section_slider_round::before {
    border-radius: 50%;
}

