.appl1_sgpt_app_container {
    width: 400px;
    height: 600px;
    overflow: hidden;
    background-color: #fff;
font-family: 'Lato', sans-serif;
}

.appl1_sgpt_app_container.desktop {
    width: 100vw;
    height: 100vh;
    overflow-y: scroll;

    .appl1_sgpt_app_settings_wrapper {
        width: 400px;
        height: auto;
        margin: 0 auto;
            
    }

    .appl1_sgpt_app_settings_provider_section {
    position: relative;
    }

    .appl1_sgpt_app_settings_provider_section_container_warningtext {
    bottom: 10px;
    left: 135px;
    }
}


.appl1_sgpt_app_container.desktop::-webkit-scrollbar-button {
    background-repeat: no-repeat;
    width: 0;
    height: 0;
}

.appl1_sgpt_app_container.desktop::-webkit-scrollbar-track {
    background-color: transparent;
    opacity: 0.5;
}

.appl1_sgpt_app_container.desktop::-webkit-scrollbar-thumb {
    border-radius: 50px;
    background-color: #592cff;
    opacity: 0.5;
}

.appl1_sgpt_app_container.desktop::-webkit-scrollbar-thumb:hover {
    background-color: #592cff;
}

.appl1_sgpt_app_container.desktop::-webkit-scrollbar {
    width: 5px;
    height: 5px;
}

.appl1_sgpt_app_header {
    display: flex;
    justify-content: space-between;
    transition: all 0.2s;
    width: 100%;
    height: 30px;
    padding: 20px 30px 0 30px;
    margin-bottom: 20px;
}

.appl1_sgpt_app_header_logobox {
    height: 100%;
    display: flex;
    align-items: center;
}

.appl1_sgpt_app_header_logobox_img {
    width: 30px;
    height: 30px;
    background-size: contain;
    background-image: url("data:image/png;base64,");
    margin-right: 10px;
}

.appl1_sgpt_app_header_logobox_text {
    font-weight: 700;
font-size: 26px;
}

.appl1_sgpt_app_header_btnbox {
    width: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
}

.appl1_sgpt_modelist_block {
    width: 30px;
    height: 30px;
    position: absolute;
    left: -5px;
}

.appl1_sgpt_app_header_modelist {
    line-height: 1.5;
    color: #000;
        background-color: #fff;
        left: -115px;
        top: 0px;
        position: absolute;
        flex-direction: column;
        justify-content: space-evenly;
        font-size: 13px;
        font-weight: 700;
        padding: 10px;
        border-radius: 5px;
        box-shadow: rgba(0, 0, 0, 0.35) 0px 2px 7px 0px;
}

.appl1_sgpt_modelist_item:hover {
    cursor: pointer;
    color: #592cff;
}

.appl1_sgpt_app_header_modebtn {
    width: 22px;
    height: 22px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url("data:image/svg+xml,%3C!-- Generated by IcoMoon.io --%3E%3Csvg version='1.1' xmlns='http://www.w3.org/2000/svg' width='60' height='60' viewBox='0 0 640 640'%3E%3Ctitle%3E%3C/title%3E%3Cg id='icomoon-ignore'%3E%3C/g%3E%3Cpath fill='%23592cff' d='M512 64h-256.672c-35.168 0-63.328 28.16-63.328 63.36v256.64c0 35.2 28.8 64 64 64h256c35.2 0 64-28.8 64-64v-256c0-35.2-28.8-64-64-64zM512 384h-256v-256h256v256zM128 320h-64v192c0 35.2 28.8 64 64 64h192v-64h-192v-192z'%3E%3C/path%3E%3C/svg%3E%0A");
    cursor: pointer;
}



.appl1_sgpt_app_header_settbtn {
    width: 22px;
    height: 22px;
    cursor: pointer;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    margin-left: auto;
